<template>
  <div id="show-blog" v-theme:column="'narrow'">
    <h1>博客总览</h1>

    <input v-model="search" type="text" placeholder="搜索">

    <div v-for="blog in filteredBlogs" class="single-blog">
      <router-link v-bind:to="'/blog/'+blog.id">
        <h2 v-rainbow>
          {{blog.title | to-uppercase}}
        </h2>
      </router-link>
      <article>
        {{blog.content | snippet}}
      </article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blog',
  data(){
    return {
      blogs:[],
      search:""
    }
  },
  created(){
    this.$http.get("https://vue-blog-c3ce2.firebaseio.com/posts.json")
    //this.$http.get("./../static/posts.json")
    .then(function(res){
      console.log(res);
      //this.blogs = res.body.slice(0,10);
      return res.json();    
    })
    .then(function(data){
      var blogArray = [];
      for (let key in data) {
        console.log(data[key]);
        data[key].id=key;
        blogArray.push(data[key]);
      }
      console.log(blogArray);
      this.blogs = blogArray;
    });
  },
  computed:{
    filteredBlogs:function(){
      return this.blogs.filter((blog)=>{
        return blog.title.match(this.search);
      })
    }
  },
  filters:{
    "to-uppercase":function(value){
      return value.toUpperCase();
    }
  },
  directives:{
    'rainbow':{
      bind(el,binding,vnode){
          el.style.color="#"+Math.random().toString(16).slice(2,8);
  
      }
    }
  }
}
</script>

<style>
#show-blog{
  max-width: 800px;
  margin: 0 auto;

}

.single-blog{
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border:1px dotted #aaa;
}

#show-blog a{
  color:#444;
  text-decoration: none;
}

input[type="text"]{
  padding: 8px;
  width:100%;
  box-sizing: border-box;
}
</style>



