<template>
  <div class="add-blog">
    <h2>{{ msg }}</h2>
    <form action="">
    <label >博客标题:</label>
    <input type="text" v-model="blog.title" required>
    <label >博客内容:</label>
    <textarea v-model="blog.content"></textarea>
    
    <div id="checkboxes">
      <label>Vue.js</label>
      <input type="checkbox" value="Vue.js" v-model="blog.categories">
      <label>Node.js</label>
      <input type="checkbox" value="Node.js" v-model="blog.categories">
      <label>React.js</label>
      <input type="checkbox" value="React.js" v-model="blog.categories">
      <label>Angular4</label>
      <input type="checkbox" value="Angular4" v-model="blog.categories">
    </div>

    <br>
    <label >作者:</label>
    <select v-model="blog.author">
        <option v-for="author in authors">
          {{author}}
        </option>
    </select>
    <button v-on:click="postBlog">添加博客</button>
    </form>

    <hr>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="category in blog.categories">
          {{category}}
        </li>
      </ul>
      <p>作者:</p>
      <p>
        {{blog.author}}
      </p>
    </div>


  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      msg: '添加博客',
      blog:{
        title:"",
        content:"",
        categories:[],
        author:""
      },
      authors:["xuejia","爱凤","幽函"],
      submmited : false
    }
  },
  methods:{
    postBlog:function(e){
      console.log("https://vue-blog-c3ce2.firebaseio.com/posts.json");
      this.$http.post("https://vue-blog-c3ce2.firebaseio.com/posts.json",this.blog).then(function(data){
        console.log(data);
        this.submmited=true;
      });
      e.preventDefault();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



